@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@500&family=Source+Code+Pro&display=swap');

@font-face {
    font-family: Panton;
    src: url(/src/font/Panton-BlackCaps.otf);
}

.App {
    text-align: center;
}

.App-header {
    background-color: #fff;
    min-height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-columns: 150px calc(100vw - 150px);
    color: white;
}

.background {
    margin: 20px 20px 20px -7vh;
    border-radius: 20px;
    background: #171717 url(/src/img/bg.jpg) no-repeat fixed right;
    background-clip: border-box;
    z-index: 0;
}

nav {
    background: #FFFFFF;
    border-radius: 0px 20px 20px 0px;
    margin: 20px 0 20px 0;
    z-index: 1;
}

nav h1 {
    position: relative;
    top: 10vh;
    left: 0;
    white-space: nowrap;
    font-family: Panton;
    font-size: 48px;
    color: #000;
    transform: rotate(90deg);
}

.App-header h2 {
    font-family: 'Montserrat Alternates', sans-serif;
    font-size: 42px;
    font-weight: 400;
}

.text p {
    font-family: 'Source Code Pro', monospace;
    font-size: 18px;
    animation: seconds 1.0s forwards;
    animation-iteration-count: 1;
    animation-delay: 1.35s;
    opacity: 0
}

@-webkit-keyframes seconds {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 1;
    }
}

@keyframes seconds {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 1;
    }
}

.App-header div {
    font-family: 'Montserrat Alternates', sans-serif;
    font-size: 14px;
}

.title {
    margin: 125px 55vw 0 calc(7vh + 90px);
    text-align: left;
}

.permanent {
    border-right: .05em solid;
    animation: caret-p 1s steps(1) infinite;
}

.temporary {
    border-right: .05em solid;
    animation: caret-h2 1s steps(1) infinite;
    animation-delay: 0.7s;
    opacity: 1;
}
.button {
    width: 10rem;
    height: 2.5rem;
    border-style: solid;
    border-width: 2px;
    border-color: white;
}
.button p {
    font-family: Montserrat Alternates;
    font-weight: 500;
    font-size: 14px;
    margin: 0;
    position: relative;
    text-align: center;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}
@keyframes caret-h2 {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 0;
    }
}

@keyframes caret-p {
    50% {
        border-color: transparent;
    }
}